<template>
    <div class="black-bg" v-if="modalIsOpened == true">
        <div class="white-bg">
            <h4>{{ roomList[modalId].title }}</h4>
            <p>{{ roomList[modalId].content }}</p>
            <!-- <input @input="month = $event.target.value"> -->
            <!-- 위의 @input="month = $event.target.value" 를 축약한 것이 아래의 v-model="month" 이다. -->
            <!-- month 의 초기값이 숫자일 지라도 나중에 문자로 반환된다. 숫자 그대로 반환하려면 v-model.number="month"  -->
            <input v-model.number="month"><label> 개월</label>
            <p> {{ month }} 개월 선택함: <span style="text-decoration-line: line-through;">{{ roomList[modalId].price *
                    month
            }} 원</span>
                {{ roomList[modalId].price * 1 / 2 * month }} 원</p>
            <transition name="discountFade">
                <Discount v-if="discountIsShowed == true"></Discount>
            </transition>
            <button @click="$emit('closeModal'); month = 1;">닫기</button>
        </div>
    </div>
</template>

<script>
import Discount from './Discount'

export default {
    name: 'Modal',
    data() {
        return {
            month: 0,
        }
    },
    watch: {
        // a 는 month 의 값으로 input 에 입력된 값
        // 파라미터를 하나 더 추가해서 month(a, b)로 만든다면 b 는 변경 전 값, a 는 변경 후의 값임
        month(a) {
            // if (typeof parseInt(a) === number) {
            //     return 0;
            // }
            if (parseInt(a) >= 13) {
                alert('13 이상 입력하지 마셈');
            }
            else if (isNaN(a) === true) {
                alert('문자 ㄴㄴ');
                this.month = 1;
            }
        },
    },
    props: {
        roomList: Array,
        modalIsOpened: Boolean,
        modalId: Number,
        discountIsShowed: Boolean,
    },
    components: {
        Discount: Discount,
    },
    beforeUpdate() {
        if (this.month == 2) {
            alert('숫자 2는 안됨ㅠㅠ');
            this.month = 3;
        }
    },
}
</script>

<style>
.discountFade-enter-to,
.discountFade-leave-from {
    opacity: 1;
}

.discountFade-enter-active,
.discountFade-leave-active {
    transition: all 0.5s;
}

.discountFade-enter-from,
.discountFade-leave-to {
    opacity: 0;
}
</style>