<template>
  <div id="wrapper">
    
    <transition name="modalFade">
      <Modal @closeModal="closeModal()" :roomList="roomList" :modalIsOpened="modalIsOpened" 
      :modalId="modalId" :discountIsShowed="discountIsShowed"></Modal>
    </transition>

    <div class="menu">
      <!-- (m, i) 에서 m은 menus의 요소들, i는 0부터 1씩 증가modalF는 인덱스 -->
      <a v-for="m in menus" :key="m" href="#"> {{ m }} </a>
    </div>

    <transition name="discountFade">
      <Discount @discountHide="discountHide()" v-if="discountIsShowed == true" :discountIsShowed="discountIsShowed"></Discount>
    </transition>

    <img class="logo" alt="Vue logo" src="./assets/logo.png">
    <br>
    <button @click="priceSort()">가격순 정렬</button>
    <button @click="sortBack()">되돌리기</button>
    <Card @openModal="openModal(i)" @reportIncrease="reportIncrease(i)" v-for="(r, i) in roomList" :key="r" :roomList="r" :reportNum="reportNum[i]"></Card>

  </div>
</template>

<script>
import roomList from './assets/roomList'
import Discount from './components/Discount'
import Modal from './components/Modal.vue';
import Card from './components/Card.vue';

export default {
  name: 'App',
  data() { // 데이터 보관함
    return {
      reportNum: [0, 0, 0, 0, 0, 0],
      menus: ['Home', 'Shops', 'Products', 'About'],
      roomList: roomList,
      roomListOg: [...roomList],
      modalIsOpened: false,
      discountIsShowed: true,
      modalId: 0,
      // blue: "color: blue;",
    }
  },
  methods: {
    reportIncrease(i) {
      this.reportNum[i]++;
    },
    openModal(i) {
      this.modalId = i;
      this.modalIsOpened = true;
    },
    closeModal() {
      this.modalIsOpened = false;
    },
    priceSort() {
      this.roomList.sort(function (a, b) {
        return a.price - b.price;
      })
    },
    sortBack() {
      this.roomList = [...this.roomListOg];
    },
    discountHide() {
      this.discountIsShowed = false;
    },
  },

  create() {

  },

  components: {
    Discount: Discount,
    Modal: Modal,
    Card: Card,
  }
}

</script>


