<template>
  <div>
    <div class="discount">
      <h4>지금 결제하면 {{ discountPer }}% 할인!</h4>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Discount',
  data() {
    return {
      discountPer: 50,
    }
  },
  props: {
    discountIsShowed: Boolean,
  },

  mounted() {
    // setTimeout(() => {
    //   this.discountIsShowed = false;
    // }, 2000)
    setInterval(() => {
      if (this.discountPer == 0) {
        this.$emit('discountHide');
        return 0;
      }
      this.discountPer -= 10;
    }, 1000)
  },
}
</script>

