<template>
    <div>
        <!-- openModal 옆에 따로 보낼 데이터를 저장한 경우 이를 받을 때 $event 로 받을 수 있다. -->
        <img :src="roomList.image" @click="$emit('openModal')" class="room-img">
        <h4>{{ roomList.title }}</h4>
        <p>{{ roomList.price }} 원</p>
        <!-- 만약 emit 의 두 번째 파라미터로 데이터를 넣는다면 부모에서 그 값을 $event 로 받을 수 있다. -->
        <button @click="$emit('reportIncrease')">신고</button> -->
        <span>신고수: {{ reportNum }}</span>
    </div>
</template>

<script>
export default {
    name: 'Card',
    props: {
        roomList: Array,
        reportNum: Number,
    },
}
</script>

